import PARENT from "../views/ParentView"
// import ONE from "../views/OneView"
// import TWO from "../views/TwoView"
import {createRouter,createWebHistory} from "vue-router";

const routes=[
    {
        path:"/parent",
        component:PARENT,
    }
]

const router=createRouter({
    mode:"history",
    history:createWebHistory(),
    routes
})

export default router